/* src/components/CharacterCreation.css */
.character-creation {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #222;
  color: #fff;
  border-radius: 10px;
}

.character-creation h2 {
  margin-bottom: 20px;
}

.character-creation input,
.character-creation select {
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  width: 100%;
  max-width: 400px;
}

.character-creation button {
  padding: 10px 20px;
  background-color: #3498db;
  border: none;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

.character-creation button:hover {
  background-color: #2980b9;
}
