/* src/components/BossFight.css */
.boss-section {
  text-align: center;
  margin-bottom: 20px;
}

.boss-image {
  max-width: 100%;
  height: auto;
}

.health-bar-container {
  width: 80%;
  margin: 0 auto;
  background-color: #ddd;
  border-radius: 25px;
  overflow: hidden;
  margin-bottom: 10px;
}

.health-bar {
  height: 25px;
  background-color: #4caf50;
  text-align: center;
  line-height: 25px;
  color: white;
  transition: width 0.5s ease-in-out;
}

.character-info {
  margin-top: 10px;
}

.chore-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #ffc107;
  margin-bottom: 10px;
  border-radius: 5px;
}

.chore-item.highlight {
  background-color: #ffeb3b;
}

.chore-item button {
  margin-left: 10px;
}

.dice-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.error {
  color: red;
  margin-bottom: 10px;
}

ul {
  list-style: none;
  padding: 0;
}

button {
  background-color: #3498db;
  border: none;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #2980b9;
}
