/* src/components/CharacterHistory.css */
.character-history {
  width: 100%;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background-color: #333;
  color: #fff;
  border-radius: 10px;
}

.character-history h3 {
  text-align: center;
  margin-bottom: 20px;
}

.character-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #444;
  border-radius: 5px;
  margin-bottom: 10px;
  cursor: pointer;
}

.character-item:hover {
  background-color: #555;
}

.delete-button {
  background-color: #e74c3c;
  border: none;
  padding: 5px 10px;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

.delete-button:hover {
  background-color: #c0392b;
}
